import * as React from "react"
import {container, pageSelect, activePage} from "../styles/topBar.module.css"

const TopBar = ({active}) => {
  return(
    <div className={container}>
      <div className={`${pageSelect} ${active==="gallery" ? activePage : undefined}`}>Gallery</div>
      <div className={`${pageSelect} ${active==="grogramming" ? activePage : undefined}`}>Programming</div>
      <div className={`${pageSelect} ${active==="GIS" ? activePage : undefined}`}>GIS</div>
      <div className={`${pageSelect} ${active==="live2D" ? activePage : undefined}`}>Live2D</div>
    </div>
  )
}

export default TopBar